.navbar{display:flex;justify-content:space-between;padding:14px 20px;background-color:#0092b8;color:#fff}.navbar ul{display:flex;align-items:center;gap:0 2.5rem}.navbar li{list-style:none;cursor:pointer}.navbar a{color:#fff;font-weight:500;text-decoration:none;transition:color .2s ease-in-out}.navbar a:hover{color:#000}